@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@700&display=swap);
* {
  margin: 0;
  padding: 0;
  text-decoration: none;
  font-size: 1em;
  outline: none;
}
code,
kbd,
samp,
pre,
tt,
var,
textarea,
input,
select,
isindex,
listing,
xmp,
plaintext {
  font: inherit;
  font-size: 1em;
  white-space: normal;
}
dfn,
i,
cite,
var,
address,
em {
  font-style: normal;
}
th,
b,
strong,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
}
a,
img,
a img,
iframe,
form,
fieldset,
abbr,
acronym,
object,
applet,
table {
  border: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
caption,
th,
td,
center {
  text-align: left;
  vertical-align: top;
}
body {
  line-height: 1;
  background: white;
  color: black;
}
q {
  quotes: "" "";
}
ul,
ol,
dir,
menu {
  list-style: none;
}
sub,
sup {
  vertical-align: baseline;
}
a {
  color: inherit;
}
hr {
  display: none;
}
font {
  color: inherit !important;
  font: inherit !important;
  color: inherit !important;
}
marquee {
  overflow: inherit !important;
  -moz-binding: none;
}
blink {
  text-decoration: none;
}
nobr {
  white-space: normal;
}


.header {
  position: fixed;
  background-color: #fff;
  width: 100%;
  top: 0;
  left: 0;
  padding: 1vh 0 1vh 0;
  z-index: 1;
}
.nav {
  font-family: 'Roboto', sans-serif;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav .nav__title {
  font-size: 22px;
  color: black;
  transition: .4s linear;
  padding: 0 4vh 0 4vh;
}

.nav .nav__title:hover {
  color: #009aff;
}

.nav .nav__icons {
  display: flex;
  justify-content: space-between;
  padding-right: 3vh;
}

.nav .nav__icons .nav__icon {
  font-size: 25px;
  color: black;
  padding: 1.5vh;
  transition: .2s linear;
}

.nav .nav__icons .github {
  transition: .3s linear;
}

.nav .nav__icons .github:hover {
  color: #717982;
}

.nav .nav__icons .kaggle {
  transition: .3s linear;
}

.nav .nav__icons .kaggle:hover {
  color: #20beff;
}

.nav .nav__icons .mail {
  transition: .3s linear;
}

.nav .nav__icons .mail:hover {
  color: #ff1e1e;
}
/*# sourceMappingURL=NavLinks.css.map */
.footer {
  background-color: black;
  width: 100%;
  border-top: solid 3px #009aff;
  transition: .3s;
  display: flex;
  font-family: 'Roboto', sans-serif;
  flex-wrap: wrap;
  padding: 1vh 0 0 0;
}

.footer .link {
  color: white;
  padding: 3vh;
  line-height: 1.5;
}

.footer .link .name {
  font-size: 4vh;
  cursor: pointer;
  padding: 3vh;
  transition: .3s;
}

.footer .link .a {
  padding: 3vh;
  cursor: pointer;
  transition: .3s;
}

.footer .link:hover {
  color: #009aff;
}

.footer:hover {
  border-top: solid 3px #44b4ff;
}
