.wrapper {
  font-family: 'Roboto', sans-serif;
}

.wrapper .parallax {
  width: 100%;
  display: flex;
  justify-content: center;
  height: 100vh;
}

.wrapper .parallax .h1 {
  font-family: 'Noto Sans', sans-serif;
  position: absolute;
  font-size: 10vh;
  font-weight: 700;
  color: white;
  margin-top: 20vh;
}

.wrapper .parallax .h2 {
  font-family: 'Noto Sans', sans-serif;
  position: absolute;
  font-size: 30px;
  font-weight: 500;
  color: white;
  margin-top: 35vh;
  flex-wrap: wrap;
  line-height: 1.5;
}

.wrapper .text {
  width: 100%;
  background: white;
  display: flex;
  justify-content: center;
}

.wrapper .text .text__p {
  font-size: 30px;
  padding: 21px;
  line-height: 1.5;
  color: black;
}

.wrapper .skills {
  margin: 2vh;
  width: 100%;
  background: white;
  display: flex;
  justify-content: space-between;
}

.wrapper .skills .div {
  width: 50%;
  padding: 2vh;
}

.wrapper .skills .div .p {
  text-align: center;
  font-size: 35px;
  padding: 2vh;
  line-height: 1.5;
  color: black;
}

.wrapper .skills .div .ul {
  max-width: 1200px;
  font-size: 21px;
  padding: 2vh;
  line-height: 1.5;
  color: black;
}

.wrapper .skills .div .ul .li {
  list-style-type: square;
  font-size: 21px;
  padding: 21px;
  line-height: 1.5;
  color: black;
}

.wrapper .cards {
  display: flex;
  justify-content: center;
  margin: 1vh;
}

.wrapper .cards .card {
  margin: 1vh;
  width: 33.3%;
}

.wrapper .cards .card .card__name {
  display: flex;
  justify-content: center;
  font-size: 20px;
  padding: 1vh;
  border: solid 1px black;
  margin: 1vh;
  transition: .4s linear;
}

.wrapper .cards .card .card__name:hover {
  color: #009aff;
  border: solid 1px #009aff;
}

.wrapper .cards .card .card__descrip {
  text-align: center;
  padding: 1vh;
  font-size: 19px;
  line-height: 1.5;
  color: black;
}

.wrapper .cards .card .card__img {
  max-width: 100%;
  object-fit: cover;
}

.wrapper .cards .card .link {
  color: #009aff;
}

@media screen and (max-width: 1200px) {
  .wrapper .parallax {
    height: calc(200px + (900 + 900 * 0.7) *  ((100vw - 320px) / 1920));
  }
  .wrapper .parallax .h1 {
    font-size: calc(20px + (80 + 80 * 0.7) *  ((100vw - 320px) / 1920));
    margin-top: calc(80px + (100 + 100 * 0.7) * ((100vw - 320px) / 1920));
  }
  .wrapper .parallax .h2 {
    font-size: calc(12px + (30 + 30 * 0.7) * ((100vw - 320px) / 1920));
    margin-top: calc(120px + (300 + 300 * 0.7) * ((100vw - 320px) / 1920));
  }
  .wrapper .skills {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
  .wrapper .skills .div {
    width: 100%;
    padding: 1vh;
    object-fit: cover;
  }
  .wrapper .text .text__p {
    font-size: calc(15px + (21 + 21 * 0.1) *  ((100vw - 320px) / 1920));
  }
}

@media screen and (max-width: 767px) {
  .wrapper .cards {
    justify-content: center;
    flex-wrap: wrap;
  }
  .wrapper .cards .card {
    width: 100%;
  }
  .wrapper .cards .card .card__descrip {
    font-size: calc(15px + (21 + 21 * 0.1) *  ((100vw - 320px) / 1920));
  }
}
/*# sourceMappingURL=Home.css.map */
